* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    
    margin: 0;
    padding: 0;        
    /*background-image: url(https://image.ibb.co/ntxD7y/wall.jpg);*/
    background-image: url(https://image.ibb.co/c4KrR8/wall4.jpg);
    /*background-image: url(https://image.ibb.co/bQykm8/wall2.jpg); */
    
    background-attachment:fixed; 
    background-position:center;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 0 0;    
    margin-top: 60px;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s; 
}

@-webkit-keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

@media screen and (max-width: 600px) {
    .row {
        margin-top: 30px;
    }
}


