.parentLoading {
    height: 1500px;          
}

.blockLoading {
    position: relative;
    width: 100px;
    height: 100px; 
    overflow: hidden;    
    z-index: 5;
    margin: 60px auto;
}

.blockLoading img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
    border: none;
}


